/* Single author box styles - begin
	====================================================================== */
$author-image-width: 94px;
.mkdf-author-description {
    @include mkdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .mkdf-author-description-inner {
        padding: 35px 0 28px;
        border-bottom: 1px solid $default-border-color;
        min-height: $author-image-width; // static height of author image -- mkdf-author-description-image
    }

    .mkdf-author-description-content {
        position: relative;
        @include mkdfTableLayout();
    }

    .mkdf-author-description-image {
        margin: 0 auto;
        @include mkdfTableCellLayout();
        width: $author-image-width;
        vertical-align: top;

        img {
            display: block;
            border-radius: 5em;
        }
    }

    .mkdf-author-description-text-holder {
        position: relative;
        @include mkdfTableCellLayout();
        width: auto;
        vertical-align: top;
        padding-left: 15px;

        .mkdf-author-name {
            margin: 0;

            a {
                color: $default-heading-color;

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .mkdf-author-email {
            margin: 0;
        }

        .mkdf-author-text {
            @include mkdfRelativeHolderLayout();

            > p {
                margin: 7px 0 9px;
            }
        }

        .mkdf-author-social-icons {
            @include mkdfRelativeHolderLayout();

            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                padding: 0;
                margin: 0 15px 0 0;
                font-size: 14px;
                color: #4f4f4f;

                &:last-child {
                    margin: 0;
                }

                &:hover {
                    color: $first-main-color;
                }
            }
        }
    }

    @include ipad-portrait {
        .mkdf-author-description-content {
            padding: 0;
        }
        .mkdf-author-description-image {
            display: block;
            width: 100%;
        }
        .mkdf-author-description-text-holder {
            display: block;
            padding: 0;
            margin-top: 30px;
        }
    }
}

/* Single author box styles - end
	====================================================================== */