/* Single posts navigation styles - begin
    ====================================================================== */

.mkdf-blog-single-navigation {
    position: relative;
    display: table;
    width: 100%;
    vertical-align: middle;
    padding: 0;
    clear: both;
    box-sizing: border-box;
    border-bottom: 1px solid #dadada;


    .mkdf-blog-single-navigation-inner {
        padding: 34px 0;
    }

    .mkdf-blog-single-nav-wrapper {
        display: inline-block;
        float: left;
        width: 50%;
        vertical-align: middle;
    }


    .mkdf-blog-single-prev,
    .mkdf-blog-single-next {
        @include mkdfTableLayout();
        min-height: 82px;
        .mkdf-blog-single-nav-image {
            @include mkdfTableCellLayout();
            width: 82px;
            line-height: 0;

            img {
                width: 82px;
                display: block;
            }
        }

        .mkdf-blog-single-nav-text {
            @include mkdfTableCellLayout();
            > span {
                display: inline-block;
                width: 100%;
            }
            .mkdf-blog-single-nav-title {
                color: #969696;
            }
        }

        &.mkdf-no-nav-image {
            .mkdf-blog-single-nav-image {
                display: none;
            }
            .mkdf-blog-single-nav-text {
                width: 100%;
                padding: 0;
            }
        }
    }

    .mkdf-blog-single-prev {
        .mkdf-blog-single-nav-text {
            padding-left: 25px;
        }
    }

    .mkdf-blog-single-next {
        text-align: right;
        .mkdf-blog-single-nav-text {
            padding-right: 25px;
        }
    }
}

.mkdf-single-links-pages {
    @include mkdfRelativeHolderLayout();
    margin: 30px 0 30px;
    padding-top: 20px;
    border-top: 1px solid $default-border-color;
    clear: both;

    .mkdf-single-links-pages-inner {

        > a,
        > span {
            position: relative;
            display: inline-block;
            vertical-align: top;
            padding: 0;
            margin: 0 5px;
            text-decoration: none;
            cursor: pointer;
            white-space: nowrap;
            color: $default-text-color;
            @include mkdfTransition(color .2s ease-in-out, border-color .2s ease-in-out);
            box-sizing: border-box;
            border: 1px solid transparent;
            line-height: 20px;
            height: 20px;
            width: 20px;
            text-align: center;
        }
        > span {
            color: $first-main-color;
            border-color: $first-main-color;
        }
        > a {
            &:hover {
                color: $first-main-color;
                border-color: $first-main-color;
            }
        }
    }
}
/* Single posts navigation styles - end
    ====================================================================== */