/* ==========================================================================
   Blog Single style - begin
   ========================================================================== */

.mkdf-blog-holder.mkdf-blog-single {
	
	article {
		margin: 0;
		
		.mkdf-post-heading {
			margin-bottom: 33px;
		}

		&.format-standard,
		&.format-video,
		&.format-gallery {
			.mkdf-post-text {
				padding: 0 0 73px;
				border-bottom: 1px solid $default-border-color;
			}
		}

        &.format-quote,
        &.format-link {
            padding: 0 0 73px;
            border-bottom: 1px solid $default-border-color;
        }

		.mkdf-post-info-bottom {
			margin-top: 28px;
		}
	}
}
/* ==========================================================================
   Blog Single style - end
   ========================================================================== */